@font-face { font-family: y3eec1; src:url(data:application/x-font-woff;charset=utf-8;base64,) format('woff');font-weight: normal;font-style: normal;}
/* theme */
.lbtn.lbtn-instagram {
    color: #ffffff;
    background-color: #e53c5c;
    border-color: #ce536a;
}

.lbtn.lbtn-instagram.white {
    color: #e53c5c;
    background-color: #ffffff;
    border-color: #d7d7d7;
}

/* logo */
.lbtn.lbtn-instagram .logo::before {
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: y3eec1;
    font-weight: normal;
    content: '\E000';
}