@font-face { font-family: Dd28b7; src:url(data:application/x-font-woff;charset=utf-8;base64,) format('woff');font-weight: normal;font-style: normal;}
/* theme */
.lbtn.lbtn-twitter {
    color: #ffffff;
    background-color: #1da1f2;
    border-color: #349ad9;
}
.lbtn.lbtn-twitter.white {
    color: #1da1f2;
    background-color: #ffffff;
    border-color: #d7d7d7;
}

/* logo */
.lbtn.lbtn-twitter .logo::before {
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: Dd28b7;
    font-weight: normal;
    content: '\E000';
}