@font-face { font-family: G2eed0; src:url(data:application/x-font-woff;charset=utf-8;base64,) format('woff');font-weight: normal;font-style: normal;}
/* theme */
.lbtn.lbtn-telegram {
    color: #ffffff;
    background-color: #37AEE2;
    border-color: #4da5cb;
}
.lbtn.lbtn-telegram.white {
    color: #37AEE2;
    background-color: #ffffff;
    border-color: #d7d7d7;
}

/* logo */
.lbtn.lbtn-telegram .logo::before {
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: G2eed0;
    font-weight: normal;
    content: '\E000';
}