@font-face { font-family: wa0b7d; src:url(data:application/x-font-woff;charset=utf-8;base64,) format('woff');font-weight: normal;font-style: normal;}
/* theme */
.lbtn.lbtn-steam {
    color: #ffffff;
    background-color: #221e1f;
    border-color: #212121;
}
.lbtn.lbtn-steam.white {
    color: #221e1f;
    background-color: #ffffff;
    border-color: #d7d7d7;
}

/* logo */
.lbtn.lbtn-steam .logo::before {
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: wa0b7d;
    font-weight: normal;
    content: '\E000';
}