@font-face { font-family: Lb2c54; src:url(data:application/x-font-woff;charset=utf-8;base64,) format('woff');font-weight: normal;font-style: normal;}
/* theme */
.lbtn.lbtn-kakao {
    color: #3c1e1e;
    background-color: #ffeb00;
    border-color: #e5d419;
}
.lbtn.lbtn-kakao.white {
    color: #3c1e1e;
    background-color: #ffffff;
    border-color: #d7d7d7;
}

/* logo */
.lbtn.lbtn-kakao .logo::before {
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: Lb2c54;
    font-weight: normal;
    content: '\E000';
}

/* filter cancel */
.lbtn:not(.white).lbtn-kakao .logo{
    filter: none;
}