@charset "UTF-8";

.lbtn {
    flex-direction: row;
    font-size: 14pt;
    font-weight: 900;
    border-radius: 8px;
    border-style: solid;
    border-width: 1px;
    display: flex;
    height: 64px;
    flex: initial;
    font-family: '\B098\B214\BC14\B978\ACE0\B515   Bold', 'Roboto', sans-serif;
    margin: 10px;
    width: 300px;
    cursor: pointer;
}

.lbtn.lbtn-flat {
    border-radius: 2px;
    flex-direction: row;
    font-family: '\B098\B214\BC14\B978\ACE0\B515   Bold', 'Roboto', sans-serif;
    font-size: 14pt;
}

.lbtn.short {
    width: 150px;
}

.lbtn.long {
    width: 450px;
}

.lbtn .logo {
    width: 64px;
    flex: initial;
    display: flex;
    position: relative;
    color: white;
    font-style: normal;
}

.lbtn .logo::before {
    font-size: 3em;
    align-self: center;
    margin: auto;
}

.lbtn.white .logo {
    color: gray;
}

.lbtn .label {
    text-align: center;
    flex: 1;
    margin: 0;
    align-items: center;
    justify-content: center;
    border-left: 1px solid;
    display: flex;
    border-color: inherit;
}

.lbtn.lbtn-flat .label {
    border-left: none;
}

.lbtn.logo-only {
    width: 64px;
}
@font-face { font-family: G1b704; src:url(data:application/x-font-woff;charset=utf-8;base64,) format('woff');font-weight: normal;font-style: normal;}
/* theme */
.lbtn.lbtn-discord {
    color: #ffffff;
    background-color: #7289DA;
    border-color: #8392c7;
}
.lbtn.lbtn-discord.white {
    color: #7289DA;
    background-color: #ffffff;
    border-color: #d7d7d7;
}

/* logo */
.lbtn.lbtn-discord .logo::before {
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: G1b704;
    font-weight: normal;
    content: '\E000';
}
