@font-face { font-family: L672ff; src:url(data:application/x-font-woff;charset=utf-8;base64,) format('woff');font-weight: normal;font-style: normal;}
/* theme */
.lbtn.lbtn-twitch {
    color: #ffffff;
    background-color: #6441A4;
    border-color: #6a588d;
}
.lbtn.lbtn-twitch.white {
    color: #6441A4;
    background-color: #ffffff;
    border-color: #d7d7d7;
}

/* logo */
.lbtn.lbtn-twitch .logo::before {
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: L672ff;
    font-weight: normal;
    content: '\E000';
}