@font-face { font-family: wdb5b2; src:url(data:application/x-font-woff;charset=utf-8;base64,) format('woff');font-weight: normal;font-style: normal;}
/* theme */
.lbtn.lbtn-facebook {
    color: #ffffff;
    background-color: #3c5a99;
    border-color: #516184;
}
.lbtn.lbtn-facebook.white {
    color: #3c5a99;
    background-color: #ffffff;
    border-color: #d7d7d7;
}

/* logo */
.lbtn.lbtn-facebook .logo::before {
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: wdb5b2;
    font-weight: normal;
    content: '\E000';
}