@font-face { font-family: zdecbb; src:url(data:application/x-font-woff;charset=utf-8;base64,) format('woff');font-weight: normal;font-style: normal;}
/* theme */
.lbtn.lbtn-wordpress {
    color: #ffffff;
    background-color: #494541;
    border-color: #564432;
}

.lbtn.lbtn-wordpress.white {
    color: #494541;
    background-color: #ffffff;
    border-color: #d7d7d7;
}

/* logo */
.lbtn.lbtn-wordpress .logo::before {
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: zdecbb;
    font-weight: normal;
    content: '\E000';
}