@font-face { font-family: L09702; src:url(data:application/x-font-woff;charset=utf-8;base64,) format('woff');font-weight: normal;font-style: normal;}
/* theme */
.lbtn.lbtn-linkedin {
    color: #ffffff;
    background-color: #0077b5;
    border-color: #116ea0;
}

.lbtn.lbtn-linkedin.white {
    color: #0077b5;
    background-color: #ffffff;
    border-color: #d7d7d7;
}

/* logo */
.lbtn.lbtn-linkedin .logo::before {
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: L09702;
    font-weight: normal;
    content: '\E000';
}