@font-face { font-family: H82952; src:url(data:application/x-font-woff;charset=utf-8;base64,) format('woff');font-weight: normal;font-style: normal;}
/* theme */
.lbtn.lbtn-google {
    color: #ffffff;
    background-color: #ea4335;
    border-color: #d3554a;
}
.lbtn.lbtn-google.white {
    color: #ea4335;
    background-color: #ffffff;
    border-color: #d7d7d7;
}

/* logo */
.lbtn.lbtn-google .logo::before {
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: H82952;
    font-weight: normal;
    content: '\E000';
}