@font-face { font-family: Leea50; src:url(data:application/x-font-woff;charset=utf-8;base64,) format('woff');font-weight: normal;font-style: normal;}
/* theme */
.lbtn.lbtn-naver {
    color: #ffffff;
    background-color: #1ec800;
    border-color: #29a011;
}

.lbtn.lbtn-naver.white {
    color: #1ec800;
    background-color: #ffffff;
    border-color: #d7d7d7;
}
.lbtn.lbtn-naver.white .label {
    color: #999;
    border-color: inherit;
}

/* logo */
.lbtn.lbtn-naver .logo::before {
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: Leea50;
    font-weight: normal;
    content: '\E000';
}