@font-face { font-family: x576d7; src:url(data:application/x-font-woff;charset=utf-8;base64,) format('woff');font-weight: normal;font-style: normal;}
/* theme */
.lbtn.lbtn-slack {
    color: #ffffff;
    background-color: #78D4B6;
    border-color: #89c2af;
}

.lbtn.lbtn-slack.white {
    color: #78D4B6;
    background-color: #ffffff;
    border-color: #d7d7d7;
}

/* logo */
.lbtn.lbtn-slack .logo::before {
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: x576d7;
    font-weight: normal;
    content: '\E000';
}