@font-face { font-family: E03e57; src:url(data:application/x-font-woff;charset=utf-8;base64,) format('woff');font-weight: normal;font-style: normal;}
/* theme */
.lbtn.lbtn-github {
    color: #ffffff;
    background-color: #1a1414;
    border-color: #181516;
}
.lbtn.lbtn-github.white {
    color: #1a1414;
    background-color: #ffffff;
    border-color: #d7d7d7;
}

/* logo */
.lbtn.lbtn-github .logo::before {
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: E03e57;
    font-weight: normal;
    content: '\E000';
}