@font-face { font-family: u8b049; src:url(data:application/x-font-woff;charset=utf-8;base64,) format('woff');font-weight: normal;font-style: normal;}
/* theme */
.lbtn.lbtn-pinterest {
    color: #ffffff;
    background-color: #BD081C;
    border-color: #ab1b2c;
}
.lbtn.lbtn-pinterest.white {
    color: #BD081C;
    background-color: #ffffff;
    border-color: #d7d7d7;
}

/* logo */
.lbtn.lbtn-pinterest .logo::before {
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: u8b049;
    font-weight: normal;
    content: '\E000';
}